}

.carousel{
    max-width: 1550px;
    /* position: relative; */
    text-align: center;
    overflow: scroll;
    overflow-y: hidden;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
}

.carousel h1 {
    font-size: 25px;
    /* margin: 40px 60px; */
    margin-top: 60px;
    margin-bottom: 60px; 
    margin-left: auto;
    margin-right: auto;
    /* min-height: 200px; */
}

#filters{
    list-style: none;
}

.carousel-item{
    transition: -webkit-transform 250ms ease-in;
    transition: transform 250ms ease-in;
    transition: transform 250ms ease-in, -webkit-transform 250ms ease-in;
    /* background-color: rgb(241, 238, 238); */
    margin-right: 15px;
}

.carousel-item:hover{
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
}

